import React, { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Card } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import api from '@/lib/api';
import { toast } from 'sonner';
import { Plus, Edit, Trash2, ChevronRight, Bot, MessageSquare, Zap } from 'lucide-react';

const VendasButtonsManager = () => {
  const [config, setConfig] = useState(null);
  const [loading, setLoading] = useState(true);
  const [showDialog, setShowDialog] = useState(false);
  const [editingButton, setEditingButton] = useState(null);
  const [parentButton, setParentButton] = useState(null);
  
  const [formData, setFormData] = useState({
    label: '',
    response_text: '',
    action_type: 'message'
  });

  useEffect(() => {
    loadConfig();
  }, []);

  const loadConfig = async () => {
    try {
      setLoading(true);
      const { data } = await api.get('/admin/vendas-bot/buttons/config');
      setConfig(data);
    } catch (error) {
      console.error('Erro ao carregar config:', error);
      // Se no existir, criar padro
      await createDefaultConfig();
    } finally {
      setLoading(false);
    }
  };

  const createDefaultConfig = async () => {
    try {
      const { data } = await api.post('/admin/vendas-bot/buttons/create-default');
      setConfig(data.config);
      toast.success('Configurao padro criada!');
    } catch (error) {
      toast.error('Erro ao criar configurao padro');
    }
  };

  const saveConfig = async () => {
    try {
      await api.post('/admin/vendas-bot/buttons/config', config);
      toast.success('Configurao salva!');
    } catch (error) {
      toast.error('Erro ao salvar configurao');
    }
  };

  const updateMode = async (newMode) => {
    try {
      await api.put('/admin/vendas-bot/buttons/mode', null, {
        params: { mode: newMode }
      });
      setConfig({...config, mode: newMode});
      toast.success(`Modo alterado para: ${newMode.toUpperCase()}`);
    } catch (error) {
      toast.error('Erro ao atualizar modo');
    }
  };

  const openAddDialog = (parent = null) => {
    setParentButton(parent);
    setEditingButton(null);
    setFormData({
      label: '',
      response_text: '',
      action_type: 'message'
    });
    setShowDialog(true);
  };

  const handleSaveButton = async () => {
    if (!formData.label || !formData.response_text) {
      toast.error('Preencha todos os campos');
      return;
    }

    try {
      const newButton = {
        id: editingButton?.id || `btn_${Date.now()}`,
        ...formData,
        sub_buttons: editingButton?.sub_buttons || []
      };

      if (editingButton) {
        // Editar boto existente
        const updateButtons = (buttons) => {
          return buttons.map(btn => {
            if (btn.id === editingButton.id) {
              return {...btn, ...formData};
            }
            if (btn.sub_buttons) {
              return {...btn, sub_buttons: updateButtons(btn.sub_buttons)};
            }
            return btn;
          });
        };
        
        setConfig({
          ...config,
          root_buttons: updateButtons(config.root_buttons)
        });
      } else {
        // Adicionar novo boto
        await api.post('/admin/vendas-bot/buttons/add-button', newButton, {
          params: { parent_id: parentButton?.id }
        });
      }

      await loadConfig();
      setShowDialog(false);
      toast.success(editingButton ? 'Boto atualizado!' : 'Boto adicionado!');
    } catch (error) {
      toast.error('Erro ao salvar boto');
    }
  };

  const handleDeleteButton = async (buttonId) => {
    if (!window.confirm('Deseja realmente excluir este boto?')) return;

    try {
      await api.delete(`/admin/vendas-bot/buttons/button/${buttonId}`);
      await loadConfig();
      toast.success('Boto removido!');
    } catch (error) {
      toast.error('Erro ao remover boto');
    }
  };

  const renderButton = (button, level = 0) => {
    return (
      <div key={button.id} className="mb-2" style={{marginLeft: `${level * 20}px`}}>
        <Card className="p-3">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-2 flex-1">
              {level > 0 && <ChevronRight className="w-4 h-4 text-gray-400" />}
              <div className="flex-1">
                <div className="font-medium">{button.label}</div>
                <div className="text-sm text-gray-600">{button.response_text.substring(0, 60)}...</div>
              </div>
            </div>
            <div className="flex gap-2">
              <Button
                size="sm"
                variant="outline"
                onClick={() => openAddDialog(button)}
              >
                <Plus className="w-4 h-4" />
              </Button>
              <Button
                size="sm"
                variant="outline"
                onClick={() => {
                  setEditingButton(button);
                  setFormData({
                    label: button.label,
                    response_text: button.response_text,
                    action_type: button.action_type
                  });
                  setShowDialog(true);
                }}
              >
                <Edit className="w-4 h-4" />
              </Button>
              <Button
                size="sm"
                variant="outline"
                onClick={() => handleDeleteButton(button.id)}
              >
                <Trash2 className="w-4 h-4 text-red-600" />
              </Button>
            </div>
          </div>
        </Card>
        
        {button.sub_buttons && button.sub_buttons.length > 0 && (
          <div className="mt-2">
            {button.sub_buttons.map(subBtn => renderButton(subBtn, level + 1))}
          </div>
        )}
      </div>
    );
  };

  if (loading) {
    return <div className="p-4">Carregando...</div>;
  }

  if (!config) {
    return (
      <div className="p-4">
        <Button onClick={createDefaultConfig}>
          Criar Configurao Padro
        </Button>
      </div>
    );
  }

  return (
    <div className="p-6 max-w-6xl">
      <div className="mb-6">
        <h2 className="text-2xl font-bold mb-4"> Sistema de Botes Interativos</h2>
        
        {/* Seletor de Modo */}
        <Card className="p-4 mb-4">
          <h3 className="font-semibold mb-3">Modo de Operao:</h3>
          <div className="flex gap-3">
            <Button
              variant={config.mode === 'button' ? 'default' : 'outline'}
              onClick={() => updateMode('button')}
              className="flex-1"
            >
              <MessageSquare className="w-4 h-4 mr-2" />
              APENAS BOTES
            </Button>
            <Button
              variant={config.mode === 'ia' ? 'default' : 'outline'}
              onClick={() => updateMode('ia')}
              className="flex-1"
            >
              <Bot className="w-4 h-4 mr-2" />
              APENAS IA
            </Button>
            <Button
              variant={config.mode === 'hibrido' ? 'default' : 'outline'}
              onClick={() => updateMode('hibrido')}
              className="flex-1"
            >
              <Zap className="w-4 h-4 mr-2" />
              HBRIDO (Botes + IA)
            </Button>
          </div>
          <p className="text-sm text-gray-600 mt-2">
            {config.mode === 'button' && ' Apenas botes interativos ativos'}
            {config.mode === 'ia' && ' Apenas IA respondendo'}
            {config.mode === 'hibrido' && ' Botes + IA funcionando juntos'}
          </p>
        </Card>

        {/* Mensagem de Boas-Vindas */}
        <Card className="p-4 mb-4">
          <label className="font-semibold block mb-2">Mensagem de Boas-Vindas:</label>
          <Textarea
            value={config.welcome_message}
            onChange={(e) => setConfig({...config, welcome_message: e.target.value})}
            rows={2}
            placeholder="Ex: Ol! Como posso ajudar voc hoje?"
          />
          <Button onClick={saveConfig} className="mt-2" size="sm">
            Salvar Mensagem
          </Button>
        </Card>

        {/* Botes */}
        <div className="flex justify-between items-center mb-4">
          <h3 className="text-xl font-semibold">Botes Configurados:</h3>
          <Button onClick={() => openAddDialog()} className="gap-2">
            <Plus className="w-4 h-4" />
            Adicionar Boto Raiz
          </Button>
        </div>

        <div className="space-y-2">
          {config.root_buttons && config.root_buttons.length > 0 ? (
            config.root_buttons.map(btn => renderButton(btn))
          ) : (
            <Card className="p-6 text-center text-gray-500">
              Nenhum boto configurado. Clique em "Adicionar Boto Raiz" para comear.
            </Card>
          )}
        </div>
      </div>

      {/* Dialog para adicionar/editar boto */}
      <Dialog open={showDialog} onOpenChange={setShowDialog}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>
              {editingButton ? 'Editar Boto' : parentButton ? 'Adicionar Sub-Boto' : 'Adicionar Boto Raiz'}
            </DialogTitle>
          </DialogHeader>
          
          <div className="space-y-4 mt-4">
            <div>
              <label className="block text-sm font-medium mb-1">Texto do Boto:</label>
              <Input
                value={formData.label}
                onChange={(e) => setFormData({...formData, label: e.target.value})}
                placeholder="Ex: SUPORTE, TESTE GRTIS"
              />
            </div>

            <div>
              <label className="block text-sm font-medium mb-1">Mensagem de Resposta:</label>
              <Textarea
                value={formData.response_text}
                onChange={(e) => setFormData({...formData, response_text: e.target.value})}
                rows={4}
                placeholder="Mensagem que ser enviada quando o boto for clicado"
              />
            </div>

            <div className="flex gap-2 justify-end">
              <Button variant="outline" onClick={() => setShowDialog(false)}>
                Cancelar
              </Button>
              <Button onClick={handleSaveButton}>
                {editingButton ? 'Atualizar' : 'Adicionar'}
              </Button>
            </div>
          </div>
        </DialogContent>
      </Dialog>
    </div>
  );
};

export default VendasButtonsManager;
